@import url("https://fonts.googleapis.com/css?family=Oswald:500");

// 
.menu-container {
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
  text-align: -webkit-center;
  background: #fff;

  .menu {
    display: flex;
    justify-content: center;
    padding: 10px;
    width: 40%;
    color: #000000;
    font-size: 1rem;
    text-align: center;
  }

  .mouse-over {
    font-family: "Oswald", sans-serif;
    font-weight: 500;
    text-transform: uppercase;
    text-decoration: none;
    color: #16151b;
    margin: 0 15px;
    font-size: 1rem;
    letter-spacing: 1px;
    position: relative;
    display: inline-block;
    cursor: pointer;
  }

  .mouse-over:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 3px;
    background: #16151b;
    top: 47%;
    animation: out 0.2s cubic-bezier(1, 0, 0.58, 0.97) 1 both;
  }

  .mouse-over:hover:before {
    animation: in 0.2s cubic-bezier(1, 0, 0.58, 0.97) 1 both;
  }

  @keyframes in {
    0% {
      width: 0;
      left: 0;
      right: auto;
    }
    100% {
      left: 0;
      right: auto;
      width: 100%;
    }
  }

  @keyframes out {
    0% {
      width: 100%;
      left: auto;
      right: 0;
    }
    100% {
      width: 0;
      left: auto;
      right: 0;
    }
  }

  @keyframes show {
    0% {
      opacity: 0;
      transform: translateY(-10px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }

  @for $i from 1 through 5 {
    nav a:nth-child(#{$i}) {
      animation: show 0.2s #{$i * 0.1+1}s ease 1 both;
    }
  }

  .selected-menu {
    font-family: "Oswald", sans-serif;
    font-weight: 500;
    text-transform: uppercase;
    text-decoration: none;
    color: #ffb000;
    margin: 0 15px;
    font-size: 1rem;
    letter-spacing: 1px;
    position: relative;
    display: inline-block;
  }
}
